.modal-franchise {
    padding: 28px;
    max-width: 280px;
    min-height: 355px;
   
   
}

@media screen and (min-width: 768px){
.modal-franchise {
    max-width: 568px;
    min-height: 458px;
    padding: 32px 60px;
}
}




.modal__title-franchise {
    margin-bottom: 20px;
 

    @include tablet() {
        margin-bottom: 24px;
        width: 322px;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
      }

}


.modal__text {
    margin-top: 0;
    margin-bottom: 24px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.4;
    color : $text-main-color ; 

    @media screen and (min-width:768px) {
        margin-bottom: 32px;

        font-size: 16px;
    }

}

.modal-form {
    display: flex;
    flex-direction: column;
}


.form-order__field {
    position: relative;
    margin-bottom: 20px;

    @media screen and (min-width: 768px) {
        width: 368px;
        margin-left: auto;
        margin-right: auto;
    }

}

.modal-form__input {
    padding: 11px 16px;
    width: 100%;
    background: $bgcolor-input;
    border: 1px solid #84A178;;
    border-radius: 10px;
    transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);


    &:focus {
        outline: none;
        border-color: $main-color;
    }

    &::placeholder {
        color: $bgcolor-input;
    }

    &:placeholder-shown {
        border-color: $bgcolor-main;
    }

  

   
}


.modal-form__label {
    position: absolute;
    left: 16px;
    top: 10px;
    font-family: 'Montserrat';
    font-size: 14px;
    line-height: 1.4;
    letter-spacing: 0.01em;
    color: $text-main-color; 
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

    .modal-form__input:focus+&,
    .modal-form__input:not(:placeholder-shown)+& {
        transform: translateY(-30px);
    }

    @media screen and (max-width: 767px) {
        font-size: 12px;
    }

}

.form-order__field--comment {
    margin-bottom: 24px;
}


.modal-form__comment {
    display: block;
    padding: 8px 12px;
    width: 100%;
    resize: none;
    background: $bgcolor-input;
    border: 1px solid $bgcolor-main;
    border-radius: 10px;
    transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:focus {
        outline: none;
        border-color: $main-color;
    }

    &::placeholder {
        color: $bgcolor-input;
    }

    &:placeholder-shown {
        border-color: $bgcolor-main;
    }

}


.modal-form__submit {
    display: flex;
    align-items: center;
    align-self: center;

    width: 114px;
    color: $text-main-color ;
    background-color: $main-color;
    cursor: pointer;
    border-width: 0;
    border-radius: 10px;
    justify-content: center;
    align-items: center;
    padding: 12px 34px;
    font-family: 'Montserrat';
    font-size: 12px;
    font-weight: 600;
    line-height: 1.25;
}